import React, { useState, useEffect } from 'react';
import { getUserName } from '../../../api/api';

export const Comment = ({ email }) => {
  const [userName, setUserName] = useState(null);

  useEffect(() => {
    getUserName(setUserName, email);
  }, []);

  return (
    <>
      {userName
        ? (
          <h6>
            User -
            {email}
          </h6>
        )
        : 'loading..'
      }
    </>
  );
};
